export interface AddressDetail {
    adminArea?: string;
    country?: string;
    countryCode?: string;
    locality?: string;
    postalCode?: string;
    streetNumber?: string;
    subAdminArea?: string;
    subLocality?: string;
    tertiaryAdminArea?: string;
    thoroughfare?: string;
}
export interface AutocompletePrediction {
    description?: string;
    matchedKeywords?: Word[];
    matchedWords?: Word[];
}
export interface ChildrenNode {
    depAndArr?: string;
    domeAndInt?: string;
    formatAddress?: string;
    hwPoiTypes?: string[];
    location?: Coordinate;
    name?: string;
    siteId?: string;
}
export interface Coordinate {
    lat: number;
    lng: number;
}
export interface CoordinateBounds {
    northeast: Coordinate;
    southwest: Coordinate;
}
export interface DetailSearchRequest {
    language?: string;
    politicalView?: string
    siteId: string;
    children?: boolean;
}
export interface DetailSearchResponse {
    site: Site;
}
export interface NearbySearchRequest {
    hwPoiType?: HwLocationType;
    language?: string;
    location: Coordinate;
    pageIndex?: number;
    pageSize?: number;
    poiType?: LocationType;
    politicalView?: string;
    query?: string;
    radius?: number;
    strictBounds?: boolean;
}
export interface NearbySearchResponse {
    sites: Site[];
    totalCount: number;
}
export interface OpeningHours {
    periods?: Period[];
    texts?: string[];
}
export interface Period {
    close?: TimeOfWeek;
    open?: TimeOfWeek;
}
export interface Poi {
    businessStatus?: string;
    childrenNodes?: ChildrenNode[];
    internationalPhone?: string;
    openingHours?: OpeningHours;
    phone?: string;
    photoUrls?: string[];
    poiTypes?: LocationType[];
    priceLevel: number;
    hwPoiTypes?: string[];
    rating?: number;
    websiteUrl?: string;
}
export interface QueryAutocompleteRequest {
    language?: string;
    location?: Coordinate;
    politicalView?: string;
    query: string;
    radius?: number;
    children?: boolean;
}
export interface QueryAutocompleteResponse {
    sites: Site[];
    predictions: AutocompletePrediction[];
}
export interface QuerySuggestionRequest {
    bounds?: CoordinateBounds;
    countryCode?: string;
    language?: string;
    location?: Coordinate;
    poiTypes?: LocationType[];
    politicalView?: string;
    query: string;
    radius?: number;
    children?: boolean;
    strictBounds?: boolean;
}
export interface QuerySuggestionResponse {
    sites: Site[];
}
export interface SearchStatus {
    errorCode: string;
    errorMessage: string;
}
export interface Site {
    address?: AddressDetail;
    distance?: number;
    formatAddress?: string;
    location?: Coordinate;
    name?: string;
    poi?: Poi;
    prediction?: AutocompletePrediction;
    siteId?: string;
    utcOffset?: number;
    viewport?: CoordinateBounds;
}
export interface TextSearchRequest {
    countryCode?: string;
    hwPoiType?: HwLocationType;
    language?: string;
    location?: Coordinate;
    pageIndex?: number;
    pageSize?: number;
    poiType?: LocationType;
    politicalView?: string;
    query: string;
    radius?: number;
    children: boolean;
}
export interface TextSearchResponse {
    sites: Site[];
    totalCount: number;
}
export interface TimeOfWeek {
    day?: number;
    time?: string;
}
export interface Word {
    offset: number;
    value: string;
}
export interface SearchFilter {
    bounds?: CoordinateBounds;
    countryCode?: string;
    language?: string;
    location?: Coordinate;
    poiType?: LocationType[];
    politicalView?: string;
    query?: string;
    radius?: number;
    strictBounds?: boolean;
    children?: boolean;
}

